h1, h2 {
    text-align: center;
}
.app {
    padding: 1em;
}
.entry-form {
    margin-bottom: 1em;
}

.entry-form .pure-button {
    margin-top: 1em;
    margin-right: 1em;
}
.entry-form label {
    display: inline-block;
    font-weight: bold;
}
.button-delete {
    background: rgb(202, 60, 60);
    color: white;
}
.entries {
    border: 2px solid #ccc;
    display: block;
    float: left;
    width: 42%;
    margin-left: 5%;
}
.entry strong {
    margin-right: 1em;
}